import "bootstrap-icons/font/bootstrap-icons.css";
// import './App.css';
// import 'antd/dist/antd.css';
import "bootstrap/dist/js/bootstrap.bundle.min.js";
import "./App.scss";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
} from "react-router-dom";
import { useState } from "react";

// VIEWS
//import Customer from './views/customer';
import SignupAd from "./components/admin/signup";
import SigninAd from "./components/admin/signin";
import ResetPasswordAd from "./components/admin/resetPassword";
import ResetPasswordSentAd from "./components/admin/recoverPassword";
import ChangePasswordAd from "./components/admin/changePassword";
import SetupAd from "./components/admin/setup";
import SetupGuideAd from "./components/admin/setUpGuide";
import HomeAd from "./components/admin/home";
import MenuAd from "./components/admin/menu";
import ProductDetailAd from "./components/admin/ProductDetail";
import CreateCategoryAd from "./components/admin/CreateCategory";
import AddItemAd from "./components/admin/AddItem";
import SetTimeSlotAd from "./components/admin/SetTimeSlot";
import AddVariantAd from "./components/admin/AddVariant";
import AddModifierAd from "./components/admin/AddModifier";
import ComboMealListAd from "./components/admin/ComboMealList";
import AddComboMealAd from "./components/admin/AddComboMeal";
import SetTimeSlotComboMealAd from "./components/admin/SetTimeSlotComboMeal";
import OrdersAd from "./components/admin/orders";
import OrderTipAd from "./components/admin/OrderTip";
import ManageTableQrAd from "./components/admin/ManageTableQr";
import StoreLocationAd from "./components/admin/storeLocation";
import LoyaltyRewardsAd from "./components/admin/loyaltyRewards";
import SetupPointsAd from "./components/admin/SetupPoints";
import { useGlobalState } from "./context";
import PrivateRoute from "./utils/PrivateRoute";
import PublicRoute from "./utils/PublicRoute";
import SetupPoints from "./components/admin/SetupPoints";
import ReedemableItemsAd from "./components/admin/ReedemableItems";
import ManageRewardValidityAd from "./components/admin/ManageRewardValidity";
// import Admin    from './views/admin';

export default function App() {
  const { currentUser } = useGlobalState();
  return (
    <Router>
      <Switch>
        <Route path="/" exact>
          {currentUser ? (
            <Redirect to="/admin/home" />
          ) : (
            <Redirect to="/admin/signin" />
          )}
        </Route>
        <Route path="/admin/signup" exact component={SignupAd} />
        <PublicRoute path="/admin/signin" exact component={SigninAd} />
        <PublicRoute
          path="/admin/reset-password"
          exact
          component={ResetPasswordAd}
        />
        <PublicRoute
          path="/admin/reset-password-sent"
          exact
          component={ResetPasswordSentAd}
        />
        <PublicRoute
          path="/admin/change-password"
          exact
          component={ChangePasswordAd}
        />
        <PrivateRoute path="/admin/setup" exact component={SetupAd} />
        <PrivateRoute
          path="/admin/setup-guide"
          exact
          component={SetupGuideAd}
        />
        <PrivateRoute path="/admin/home" exact component={HomeAd} />
        <PrivateRoute path="/admin/menu" exact component={MenuAd} />
        <PrivateRoute
          path="/admin/menu/product-detail"
          exact
          component={ProductDetailAd}
        />
        <PrivateRoute
          path="/admin/menu/create-category"
          exact
          component={CreateCategoryAd}
        />
        <PrivateRoute path="/admin/menu/add-item" exact component={AddItemAd} />
        <PrivateRoute
          path="/admin/menu/add-item/set-time-slot"
          exact
          component={SetTimeSlotAd}
        />
        <PrivateRoute
          path="/admin/menu/add-variants"
          exact
          component={AddVariantAd}
        />
        <PrivateRoute
          path="/admin/menu/add-modifier"
          exact
          component={AddModifierAd}
        />
        <PrivateRoute
          path="/admin/menu/combo-meal"
          exact
          component={ComboMealListAd}
        />
        <PrivateRoute
          path="/admin/menu/combo-meal/add-combo-meal"
          exact
          component={AddComboMealAd}
        />
        <PrivateRoute
          path="/admin/menu/combo-meal/set-time-slot"
          exact
          component={SetTimeSlotComboMealAd}
        />
        <PrivateRoute path="/admin/orders" exact component={OrdersAd} />
        <PrivateRoute
          path="/admin/orders/order-tipping"
          exact
          component={OrderTipAd}
        />
        <PrivateRoute
          path="/admin/orders/manage-table-qr"
          exact
          component={ManageTableQrAd}
        />
        <PrivateRoute
          path="/admin/store-location"
          exact
          component={StoreLocationAd}
        />
        <PrivateRoute
          path="/admin/loyalty-rewards"
          exact
          component={LoyaltyRewardsAd}
        />
        <PrivateRoute
          path="/admin/loyalty-rewards/setup-points"
          exact
          component={SetupPointsAd}
        />
        <PrivateRoute
          path="/admin/loyalty-rewards/setup-reedemable-items"
          exact
          component={ReedemableItemsAd}
        />
        <PrivateRoute
          path="/admin/loyalty-rewards/manage-rewards-validity"
          exact
          component={ManageRewardValidityAd}
        />
      </Switch>
    </Router>
  );
}
