import React from "react";
import { FaRegBell, FaBars } from "react-icons/fa";
import {
  BiMessageSquareDetail,
  BiLogOutCircle,
  BiLogOut,
} from "react-icons/bi";
import { AiOutlineSetting } from "react-icons/ai";
import "./admin.css";
import { useGlobalState } from "../../context";
import { useHistory } from "react-router";
function Navbar(props) {
  const { showSidebar, setShowSidebar, logout } = useGlobalState();
  const history = useHistory();
  const handleLogout = () => {
    if (logout()) {
      history.push("/admin/signin");
    }
  };
  return (
    <div className="navbar-container navbar-container-md">
      <FaBars
        style={{ fontSize: "20px" }}
        className="navbar-item menu-icon"
        onClick={() => setShowSidebar(!showSidebar)}
      />
      <div className="navbar-item d-flex align-items-center">
        <div className="badge-nav me-2">
          <FaRegBell />
          <span></span>
        </div>
        <div className="badge-nav me-2">
          <BiMessageSquareDetail />
          <span></span>
        </div>
        <div className="badge-nav">
          <AiOutlineSetting />
          <span></span>
        </div>
      </div>
      <div className="navbar-item d-flex flex-column">
        <div>
          <div style={{ fontSize: "14px", color: "#9C9C9C" }}>Good Morning</div>
          <div style={{ fontWeight: "bold", fontSize: "16px" }}>Tiger Puff</div>
        </div>
        <div
          style={{ fontSize: "14px", color: "#9C9C9C", cursor: "pointer" }}
          onClick={handleLogout}
          className="d-flex align-items-center"
        >
          <span className="px-1">Log out</span>
          <BiLogOutCircle />
        </div>
      </div>
      <div className="navbar-item display-none d-flex align-items-center">
        <div className="avatar"></div>
      </div>
    </div>
  );
}

export default Navbar;
