import React, { useEffect, useState } from 'react';
import Navbar from './Navbar';
import Sidebar from './Sidebar';
import { BsSearch } from 'react-icons/bs';
import { BiPencil } from 'react-icons/bi';
import { AiFillCaretDown, AiFillCaretUp } from 'react-icons/ai';
import { PaginationFunction } from './PaginationFunction';
import { SortFunction } from './SortFunction';
import { searchFunction } from './SearchFunction';
import AddStore from './AddStore';
import axios from 'axios';
import { useGlobalState } from '../../context';
function StoreLocation(props) {
  const API_URL = process.env.REACT_APP_API_URL;
  const { currentUser } = useGlobalState();
  const [data, setData] = useState([]);
  // pagination
  const { currentItems, pagination } = PaginationFunction(data, 10);
  // sort table
  const { items, requestSort, getClassNamesFor } = SortFunction(currentItems);
  //select id
  const [selectIds, setSelectIds] = useState([]);
  const handleSelect = (id) => {
    let copy = [...selectIds];
    let check = copy.find((item) => {
      return item === id;
    });
    if (check === undefined) {
      copy.push(id);
    } else {
      copy.splice(copy.indexOf(id), 1);
    }
    console.log(copy);
    setSelectIds(copy);
  };
  const handleDelete = () => {
    let copy = [...data];
    selectIds.forEach((id) => {
      copy = copy.filter((item) => {
        return item.id !== id;
      });
    });
    setData(copy);
  };
  //
  const getData = async () => {
    let res = await axios.get(`${API_URL}/api/stores/`, {
      headers: {
        Authorization: `Bearer ${currentUser.access_token}`,
      },
    });
    setData(res.data.data);
  };
  useEffect(() => {
    getData();
  }, []);
  return (
    <div className='wrapper'>
      <Sidebar />
      <div className='content'>
        <Navbar />
        <div className='container-fluid mainview-container'>
          <div className='row d-flex align-items-center'>
            <div className='col-lg-5 mb-2'>
              <h2 className='mb-1'>Store Location</h2>
              <div style={{ color: '#828282' }}>Home/Store Location</div>
            </div>
            <div className='col-lg-3 mb-2'>
              <div className='form-control rounded-pill d-flex align-items-center'>
                <input
                  type='text'
                  placeholder='search here'
                  style={{ outline: 'none' }}
                  name=''
                  id='myInput'
                  onKeyUp={() => searchFunction('myInput', 'myTable')}
                  className='w-100 border-0'
                />
                <BsSearch />
              </div>
            </div>
            <div className='col-lg-2 mb-2'>
              <button
                type='button'
                className='form-control rounded-pill btn-primary'
                data-bs-toggle='modal'
                data-bs-target='#staticBackdrop'
              >
                Add Store
              </button>
            </div>
            <div className='col-lg-2 mb-2'>
              <button
                className='form-control rounded-pill btn-primary'
                onClick={handleDelete}
              >
                Delete
              </button>
            </div>
          </div>
          <div style={{ width: '100%', overflowX: 'auto' }}>
            <table
              style={{
                minHeight: data.length ? '' : '500px',
                minWidth: '500px',
              }}
              className='table table-striped rounded shadow mt-2'
            >
              <thead>
                <tr>
                  <th scope='col' className='border-0'>
                    #
                  </th>
                  <th scope='col' className='border-0'>
                    <button
                      type='button'
                      onClick={() => requestSort('name')}
                      className='d-flex align-item-center border-0 bg-transparent'
                    >
                      <span className='fw-bold'>Store Name</span>
                      <span className='d-flex flex-column align-item-center justify-content-center sort-icon'>
                        <AiFillCaretUp
                          className={
                            getClassNamesFor('name') === 'descending'
                              ? 'descending'
                              : undefined
                          }
                        />
                        <AiFillCaretDown
                          className={
                            getClassNamesFor('name') === 'ascending'
                              ? 'ascending'
                              : undefined
                          }
                        />
                      </span>
                    </button>
                  </th>
                  <th scope='col' className='border-0'>
                    Address
                  </th>
                  <th scope='col' className='border-0'>
                    Phone
                  </th>
                  <th scope='col' className='border-0'></th>
                </tr>
              </thead>
              <tbody id='myTable'>
                {items.map((item, index) => {
                  return (
                    <tr>
                      <td>
                        <input
                          className='form-check-input'
                          type='checkbox'
                          value=''
                          checked={
                            selectIds.find((id) => {
                              return id === item.id;
                            }) !== undefined
                              ? true
                              : false
                          }
                          onChange={() => handleSelect(item.id)}
                        />
                      </td>
                      <td>{item.name}</td>
                      <td>{item.address}</td>
                      <td>{item.phone}</td>
                      <td>
                        <BiPencil />
                      </td>
                    </tr>
                  );
                })}
              </tbody>
            </table>
          </div>
          {pagination}
          <div
            className='modal fade'
            id='staticBackdrop'
            data-bs-backdrop='static'
            data-bs-keyboard='false'
            tabIndex='-1'
            aria-labelledby='staticBackdropLabel'
            aria-hidden='true'
          >
            <div className='modal-dialog modal-lg modal-dialog-centered'>
              <AddStore getData={getData} />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default StoreLocation;
