import React, { useState } from 'react';
import Navbar from './Navbar';
import Sidebar from './Sidebar';

function SetupPoints(props) {
  //store list
  const [storeList, setStoreList] = useState([
    {
      id: '1',
      name: 'TigerPuff',
    },
    {
      id: '2',
      name: 'McDonalds',
    },
    {
      id: '3',
      name: 'Jollibee',
    },
    {
      id: '4',
      name: 'KFC',
    },
  ]);
  const [selectStoreIds, setSelectStoreIds] = useState([]);
  const [selectStoreNames, setSelectStoreNames] = useState([]);
  const handleSelectStore = (id, name, selectAll) => {
    if (!selectAll) {
      let copyId = [...selectStoreIds];
      let copyName = [...selectStoreNames];
      let check = copyId.find((item) => {
        return item === id;
      });
      if (check === undefined) {
        copyId.push(id);
        copyName.push(name);
      } else {
        copyId.splice(copyId.indexOf(id), 1);
        copyName.splice(copyName.indexOf(name), 1);
      }
      setSelectStoreIds(copyId);
      setSelectStoreNames(copyName);
    } else {
      if (storeList.length !== selectStoreIds.length) {
        let copy = [...storeList];
        let storeIds = copy.map((item) => {
          return item.id;
        });
        let storeNames = copy.map((item) => {
          return item.name;
        });
        setSelectStoreIds(storeIds);
        setSelectStoreNames(storeNames);
      } else {
        setSelectStoreIds([]);
        setSelectStoreNames([]);
      }
    }
  };
  return (
    <div className='wrapper'>
      <Sidebar />
      <div className='content'>
        <Navbar />
        <div className='container-fluid mainview-container'>
          <div className='row d-flex align-items-center'>
            <div className='col-lg-5 mb-2'>
              <h2 className='mb-1'>Loyalty Rewards</h2>
              <div style={{ color: '#828282' }}>
                Home/Loyalty Rewards/Setup Points
              </div>
            </div>
          </div>

          <form className='container-fluid shadow rounded'>
            <div className='border-bottom py-2 px-2 fw-bold'>Setup Points</div>
            <div className='row px-2 py-4'>
              <div className='col-md-8 d-flex flex-column justify-content-between py-2'>
                <div className='mb-3'>
                  <label className='form-label text-gray'>
                    Enable Loyalty Rewards on Total Bill
                  </label>
                  <div>
                    <div className='form-check form-check-inline'>
                      <input
                        className='form-check-input'
                        type='radio'
                        name='inlineRadioOptions'
                        id='inlineRadio1'
                        value='option1'
                      />
                      <label className='form-check-label' for='inlineRadio1'>
                        Yes
                      </label>
                    </div>
                    <div className='form-check form-check-inline ms-5'>
                      <input
                        className='form-check-input'
                        type='radio'
                        name='inlineRadioOptions'
                        id='inlineRadio2'
                        value='option2'
                      />
                      <label className='form-check-label' for='inlineRadio2'>
                        No
                      </label>
                    </div>
                  </div>
                </div>
                <div className='form-check mb-3'>
                  <input
                    className='form-check-input'
                    type='checkbox'
                    value=''
                    id='flexCheckChecked'
                  />
                  <label
                    className='form-check-label text-gray'
                    for='flexCheckChecked'
                  >
                    Set Rewards per Amount
                  </label>
                </div>
                <div className='row'>
                  <div className='col-5'>
                    <div class='input-group'>
                      <input type='text' class='form-control' />
                      <div class='input-group-text bg-gray'>$</div>
                    </div>
                  </div>
                  <div className='col-2 d-flex justify-content-center'>
                    <div
                      style={{ maxWidth: '60px', color: 'white' }}
                      className='bg-gray w-100 h-100 rounded text-center d-flex align-items-center justify-content-center'
                    >
                      <span>=</span>
                    </div>
                  </div>
                  <div className='col-5'>
                    <div class='input-group'>
                      <input type='text' class='form-control' />
                      <div class='input-group-text bg-gray'>Points</div>
                    </div>
                  </div>
                </div>
              </div>
              <div className='col-md-4 py-2'>
                <label className='form-label d-flex justify-content-between text-gray'>
                  <span>Select Store</span>
                </label>
                <div className='card'>
                  <div className='card-header d-flex justify-content-between'>
                    <span>Restaurant</span>
                    <span>
                      Mark All
                      <input
                        className='form-check-input ms-3'
                        type='checkbox'
                        checked={
                          selectStoreIds.length === storeList.length
                            ? true
                            : false
                        }
                        onChange={() => handleSelectStore(null, null, true)}
                      />
                    </span>
                  </div>
                  <ul
                    style={{ height: '116px', overflow: 'auto' }}
                    className='list-group list-group-flush'
                  >
                    {storeList.map((item) => {
                      return (
                        <li className='list-group-item d-flex justify-content-between'>
                          <span>{item.name}</span>
                          <input
                            className='form-check-input'
                            type='checkbox'
                            checked={
                              selectStoreIds.find((id) => {
                                return id === item.id;
                              }) !== undefined
                                ? true
                                : false
                            }
                            onChange={() =>
                              handleSelectStore(item.id, item.name, false)
                            }
                          />
                        </li>
                      );
                    })}
                  </ul>
                </div>
              </div>
            </div>
            <div className='row px-2 py-4'>
              <div className='col-lg-2 col-md-4 ms-auto'>
                <button type='submit' className='form-control btn-outline-dark'>
                  Save
                </button>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
}

export default SetupPoints;
