import React, { useState } from 'react';
import Navbar from './Navbar';
import Sidebar from './Sidebar';
import vector from '../../assets/images/Vector.png';
import { RiDeleteBin6Line } from 'react-icons/ri';
import { BiPencil } from 'react-icons/bi';
function LoyaltyRewards(props) {
  //store list
  const [storeList, setStoreList] = useState([
    {
      id: '1',
      name: 'TigerPuff',
    },
    {
      id: '2',
      name: 'McDonalds',
    },
    {
      id: '3',
      name: 'Jollibee',
    },
    {
      id: '4',
      name: 'KFC',
    },
  ]);
  const [selectStoreIds, setSelectStoreIds] = useState([]);
  const [selectStoreNames, setSelectStoreNames] = useState([]);
  const handleSelectStore = (id, name, selectAll) => {
    if (!selectAll) {
      let copyId = [...selectStoreIds];
      let copyName = [...selectStoreNames];
      let check = copyId.find((item) => {
        return item === id;
      });
      if (check === undefined) {
        copyId.push(id);
        copyName.push(name);
      } else {
        copyId.splice(copyId.indexOf(id), 1);
        copyName.splice(copyName.indexOf(name), 1);
      }
      setSelectStoreIds(copyId);
      setSelectStoreNames(copyName);
    } else {
      if (storeList.length !== selectStoreIds.length) {
        let copy = [...storeList];
        let storeIds = copy.map((item) => {
          return item.id;
        });
        let storeNames = copy.map((item) => {
          return item.name;
        });
        setSelectStoreIds(storeIds);
        setSelectStoreNames(storeNames);
      } else {
        setSelectStoreIds([]);
        setSelectStoreNames([]);
      }
    }
  };
  return (
    <div className='wrapper'>
      <Sidebar />
      <div className='content'>
        <Navbar />
        <div className='container-fluid mainview-container'>
          <div className='row d-flex align-items-center'>
            <div className='col-lg-5 mb-2'>
              <h2 className='mb-1'>Loyalty Rewards</h2>
              <div style={{ color: '#828282' }}>Home/Loyalty Rewards</div>
            </div>
            <div className='offset-lg-3 col-lg-2 mb-2'>
              <button
                type='button'
                className='form-control rounded-pill btn-primary'
                data-bs-toggle='modal'
                data-bs-target='#staticBackdrop'
              >
                Add VIP Level
              </button>
            </div>
            <div className='col-lg-2 mb-2'>
              <button className='form-control rounded-pill btn-primary'>
                Delete
              </button>
            </div>
          </div>
          <div className='row'>
            <div className='col-md-4 mb-2'>
              <div className='loyalty-card silver'>
                <img src={vector} alt='' />
                <div
                  style={{ color: '#828282' }}
                  className='w-100 d-flex justify-content-between'
                >
                  <BiPencil />
                  <RiDeleteBin6Line />
                </div>
                <div className='w-100 d-flex flex-column align-items-center justify-content-center'>
                  <div
                    style={{
                      color: '#515151',
                      fontWeight: '600',
                      fontSize: '18px',
                    }}
                  >
                    Silver
                  </div>
                  <div
                    style={{
                      color: '#828282',
                      fontSize: '16px',
                    }}
                  >
                    1000 points
                  </div>
                </div>
              </div>
            </div>
            <div className='col-md-4 mb-2'>
              <div className='loyalty-card gold'>
                <img src={vector} alt='' />
                <div
                  style={{ color: '#828282' }}
                  className='w-100 d-flex justify-content-between'
                >
                  <BiPencil />
                  <RiDeleteBin6Line />
                </div>
                <div className='w-100 d-flex flex-column align-items-center justify-content-center'>
                  <div
                    style={{
                      color: '#515151',
                      fontWeight: '600',
                      fontSize: '18px',
                    }}
                  >
                    Gold
                  </div>
                  <div
                    style={{
                      color: '#828282',
                      fontSize: '16px',
                    }}
                  >
                    2000 points
                  </div>
                </div>
              </div>
            </div>
            <div className='col-md-4 mb-2'>
              <div className='loyalty-card platinum'>
                <img src={vector} alt='' />
                <div
                  style={{ color: '#828282' }}
                  className='w-100 d-flex justify-content-between'
                >
                  <BiPencil />
                  <RiDeleteBin6Line />
                </div>
                <div className='w-100 d-flex flex-column align-items-center justify-content-center'>
                  <div
                    style={{
                      color: '#515151',
                      fontWeight: '600',
                      fontSize: '18px',
                    }}
                  >
                    Platinum
                  </div>
                  <div
                    style={{
                      color: '#828282',
                      fontSize: '16px',
                    }}
                  >
                    3500 points
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div
            className='modal fade'
            id='staticBackdrop'
            data-bs-backdrop='static'
            data-bs-keyboard='false'
            tabIndex='-1'
            aria-labelledby='staticBackdropLabel'
            aria-hidden='true'
          >
            <div className='modal-dialog modal-dialog-centered'>
              <div className='modal-content'>
                <div className='modal-header px-3 py-1'>
                  <h5
                    style={{ fontSize: '16px' }}
                    className='modal-title'
                    id='staticBackdropLabel'
                  >
                    VIP Level
                  </h5>
                </div>
                <form action='' id='form'>
                  <div className='modal-body'>
                    <div className='mb-3'>
                      <label className='form-label text-gray'>VIP Level</label>
                      <input
                        className='form-control'
                        placeholder='Enter VIP level ...'
                        required
                      />
                    </div>
                    <div className='mb-3'>
                      <label className='form-label text-gray'>
                        Total Earned Points
                      </label>
                      <input className='form-control' placeholder='' required />
                    </div>
                    <div className='mb-3'>
                      <label className='form-label d-flex justify-content-between'>
                        <span>Select Store</span>
                      </label>
                      <div className='card'>
                        <div className='card-header d-flex justify-content-between'>
                          <span>Restaurant</span>
                          <span>
                            Mark All
                            <input
                              className='form-check-input ms-3'
                              type='checkbox'
                              checked={
                                selectStoreIds.length === storeList.length
                                  ? true
                                  : false
                              }
                              onChange={() =>
                                handleSelectStore(null, null, true)
                              }
                            />
                          </span>
                        </div>
                        <ul
                          style={{ height: '116px', overflow: 'auto' }}
                          className='list-group list-group-flush'
                        >
                          {storeList.map((item) => {
                            return (
                              <li className='list-group-item d-flex justify-content-between'>
                                <span>{item.name}</span>
                                <input
                                  className='form-check-input'
                                  type='checkbox'
                                  checked={
                                    selectStoreIds.find((id) => {
                                      return id === item.id;
                                    }) !== undefined
                                      ? true
                                      : false
                                  }
                                  onChange={() =>
                                    handleSelectStore(item.id, item.name, false)
                                  }
                                />
                              </li>
                            );
                          })}
                        </ul>
                      </div>
                    </div>
                  </div>
                  <div className='modal-footer'>
                    <div className='col-md-4 px-1'>
                      <button
                        type='button'
                        className='form-control btn-outline-dark'
                        data-bs-dismiss='modal'
                      >
                        Cancel
                      </button>
                    </div>
                    <div className='col-md-4 px-1'>
                      <button
                        type='submit'
                        className='form-control btn-outline-dark'
                      >
                        Save
                      </button>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default LoyaltyRewards;
