import React, { useEffect, useState } from 'react';
import { BsSearch } from 'react-icons/bs';
import { BiPencil } from 'react-icons/bi';
import {
  AiFillCaretDown,
  AiFillCaretUp,
  AiOutlinePlusCircle,
  AiOutlineMinusCircle,
} from 'react-icons/ai';
import { PaginationFunction } from './PaginationFunction';
import { SortFunction } from './SortFunction';
import Sidebar from './Sidebar';
import Navbar from './Navbar';
import { searchFunction } from './SearchFunction';
import axios from 'axios';
import { useGlobalState } from '../../context';
function CreateCategory(props) {
  const API_URL = process.env.REACT_APP_API_URL;
  const { currentUser } = useGlobalState();
  const [data, setData] = useState([]);
  // pagination
  const { currentItems, pagination } = PaginationFunction(data, 10);
  // sort table
  const { items, requestSort, getClassNamesFor } = SortFunction(currentItems);
  //select id
  const [selectIds, setSelectIds] = useState([]);
  const handleSelect = (id) => {
    let copy = [...selectIds];
    let check = copy.find((item) => {
      return item === id;
    });
    if (check === undefined) {
      copy.push(id);
    } else {
      copy.splice(copy.indexOf(id), 1);
    }
    console.log(copy);
    setSelectIds(copy);
  };
  const handleDelete = async () => {
    try {
      let res = await Promise.all(
        selectIds.map((item) => {
          axios.delete(`${API_URL}/api/menus/categories/${item}`, {
            headers: {
              Authorization: `Bearer ${currentUser.access_token}`,
            },
          });
        })
      );
      let copy = [...data];
      selectIds.forEach((id) => {
        copy = copy.filter((item) => {
          return item.id !== id;
        });
      });
      setData(copy);
    } catch (err) {
      console.log(err);
    }
  };
  //handle set category name
  const [category, setCategory] = useState(null);
  const handleSetCategory = (value) => {
    setCategory(value);
  };
  //store list & handle select store
  const [storeList, setStoreList] = useState([]);
  const [selectStoreIds, setSelectStoreIds] = useState([]);
  const handleSelectStore = (id, name, selectAll) => {
    if (!selectAll) {
      let copyId = [...selectStoreIds];

      let check = copyId.find((item) => {
        return item === id;
      });
      if (check === undefined) {
        copyId.push(id);
      } else {
        copyId.splice(copyId.indexOf(id), 1);
      }
      setSelectStoreIds(copyId);
    } else {
      if (storeList.length !== selectStoreIds.length) {
        let copy = [...storeList];
        let storeIds = copy.map((item) => {
          return item.id;
        });

        setSelectStoreIds(storeIds);
      } else {
        setSelectStoreIds([]);
      }
    }
  };
  //handle set sub category
  const [subCategory, setSubCategory] = useState([null]);
  const handlePlusSubCategory = () => {
    let copy = [...subCategory];
    copy.push(null);
    setSubCategory(copy);
  };
  const handleMinusSubCategory = (index) => {
    let copy = [...subCategory];
    if (copy.length > 1) {
      copy.splice(index, 1);
      setSubCategory(copy);
    }
  };
  const handleSetSubCategory = (value, index) => {
    console.log(value);
    let copy = [...subCategory];
    copy[index] = value;
    setSubCategory(copy);
  };
  //
  const [updatingItem, setUpdatingItem] = useState(null);
  const handleSetUpdatingItem = (item) => {
    setUpdatingItem(item);
    setCategory(item.name);
    let sub_categories = item.sub_categories.map((sub) => {
      return sub.name;
    });
    setSubCategory(sub_categories);
  };
  //handle create category
  const handleCreateCategory = async (event) => {
    event.preventDefault();
    let bodyData = {
      name: category,
      list_sub_category: subCategory,
      list_store_id: selectStoreIds,
    };
    try {
      let res = await axios.post(`${API_URL}/api/menus/categories`, bodyData, {
        headers: {
          Authorization: `Bearer ${currentUser.access_token}`,
        },
      });
      console.log(res.data);
      handleCancel();
      getCategoryList();
    } catch (err) {
      console.log(err);
    }
  };
  //
  const handleUpdateCategory = async (event) => {
    event.preventDefault();
    let bodyData = {
      name: category,
      list_sub_category: subCategory,
      list_store_id: selectStoreIds,
    };
    try {
      let res = await axios.patch(
        `${API_URL}/api/menus/categories/${updatingItem.id}`,
        bodyData,
        {
          headers: {
            Authorization: `Bearer ${currentUser.access_token}`,
          },
        }
      );
      console.log(res.data);
    } catch (err) {
      console.log(err);
    }
    setUpdatingItem(null);
    handleCancel();
    getCategoryList();
  };
  const handleCancel = (event) => {
    document.getElementById('form').reset();
    setCategory(null);
    setSubCategory([null]);
    setSelectStoreIds([]);
  };
  //get store list
  const getStoreList = async () => {
    try {
      let res = await axios.get(`${API_URL}/api/stores/`, {
        headers: {
          Authorization: `Bearer ${currentUser.access_token}`,
        },
      });
      setStoreList(res.data.data);
    } catch (err) {
      console.log(err);
    }
  };
  //get category list
  const getCategoryList = async () => {
    try {
      let res = await axios.get(`${API_URL}/api/menus/categories`, {
        headers: {
          Authorization: `Bearer ${currentUser.access_token}`,
        },
      });
      setData(res.data.data);
    } catch (err) {
      console.log(err);
    }
  };
  useEffect(() => {
    getCategoryList();
    getStoreList();
  }, []);
  return (
    <div className='wrapper'>
      <Sidebar />
      <div className='content'>
        <Navbar />
        <div className='container-fluid mainview-container'>
          <div className='row d-flex align-items-center'>
            <div className='col-lg-5 mb-2'>
              <h2 className='mb-1'>Menu</h2>
              <div style={{ color: '#828282' }}>Home/Menu/Category List</div>
            </div>
            <div className='col-lg-3 mb-2'>
              <div className='form-control rounded-pill d-flex align-items-center'>
                <input
                  type='text'
                  placeholder='search here'
                  style={{ outline: 'none' }}
                  name=''
                  id='myInput'
                  onKeyUp={() => searchFunction('myInput', 'myTable')}
                  className='w-100 border-0'
                />
                <BsSearch />
              </div>
            </div>
            <div className='col-lg-2 mb-2'>
              <button
                type='button'
                className='form-control rounded-pill btn-primary'
                data-bs-toggle='modal'
                data-bs-target='#addCategory'
              >
                Add Category
              </button>
            </div>
            <div className='col-lg-2 mb-2'>
              <button
                className='form-control rounded-pill btn-primary'
                onClick={handleDelete}
              >
                Delete
              </button>
            </div>
          </div>
          <div style={{ width: '100%', overflowX: 'auto' }}>
            <table
              style={{
                minHeight: data.length ? '' : '500px',
                minWidth: '500px',
              }}
              className='table table-striped rounded shadow mt-2'
            >
              <thead>
                <tr>
                  <th scope='col' className='border-0'>
                    #
                  </th>
                  <th scope='col' className='border-0'>
                    <button
                      type='button'
                      onClick={() => requestSort('name')}
                      className='d-flex align-item-center border-0 bg-transparent'
                    >
                      <span className='fw-bold'>Category</span>
                      <span className='d-flex flex-column align-item-center justify-content-center sort-icon'>
                        <AiFillCaretUp
                          className={
                            getClassNamesFor('name') === 'descending'
                              ? 'descending'
                              : undefined
                          }
                        />
                        <AiFillCaretDown
                          className={
                            getClassNamesFor('name') === 'ascending'
                              ? 'ascending'
                              : undefined
                          }
                        />
                      </span>
                    </button>
                  </th>
                  <th scope='col' className='border-0'>
                    Sub Category
                  </th>
                  <th scope='col' className='border-0'>
                    Store
                  </th>
                  <th scope='col' className='border-0'></th>
                </tr>
              </thead>
              <tbody id='myTable'>
                {items.map((item, index) => {
                  return (
                    <tr>
                      <td>
                        <input
                          className='form-check-input'
                          type='checkbox'
                          value=''
                          checked={
                            selectIds.find((id) => {
                              return id === item.id;
                            }) !== undefined
                              ? true
                              : false
                          }
                          onChange={() => handleSelect(item.id)}
                        />
                      </td>
                      <td>{item.name}</td>
                      <td>
                        {item.sub_categories &&
                          item.sub_categories
                            .map((sub) => {
                              return sub.name;
                            })
                            .toString()}
                      </td>
                      <td>{item?.store}</td>
                      <td>
                        <BiPencil
                          className='pointer'
                          data-bs-toggle='modal'
                          data-bs-target='#updateCategory'
                          onClick={() => handleSetUpdatingItem(item)}
                        />
                      </td>
                    </tr>
                  );
                })}
              </tbody>
            </table>
          </div>
          {pagination}
          <div
            className='modal fade'
            id='addCategory'
            data-bs-backdrop='static'
            data-bs-keyboard='false'
            tabIndex='-1'
            aria-hidden='true'
          >
            <div className='modal-dialog modal-dialog-centered'>
              <div className='modal-content'>
                <div className='modal-header px-3 py-1'>
                  <h5 style={{ fontSize: '16px' }} className='modal-title'>
                    Add Category
                  </h5>
                </div>
                <form action='' onSubmit={handleCreateCategory} id='form'>
                  <div className='modal-body'>
                    <div className='mb-3'>
                      <label className='form-label'>Category Name</label>
                      <input
                        className='form-control'
                        placeholder='enter category name ...'
                        onChange={(e) => handleSetCategory(e.target.value)}
                        required
                      />
                    </div>
                    <div className='mb-3'>
                      <label className='form-label'>
                        Select Store Location
                      </label>
                      <div>
                        {storeList.map((item) => {
                          return (
                            <div className='form-check form-check-inline'>
                              <input
                                className='form-check-input'
                                type='checkbox'
                                value=''
                                checked={
                                  selectStoreIds.find((id) => {
                                    return id === item.id;
                                  }) !== undefined
                                    ? true
                                    : false
                                }
                                onChange={() =>
                                  handleSelectStore(item.id, item.name, false)
                                }
                              />
                              <label className='form-check-label'>
                                {item.name}
                              </label>
                            </div>
                          );
                        })}
                        {storeList.length !== 0 && (
                          <div className='form-check form-check-inline'>
                            <input
                              className='form-check-input'
                              type='checkbox'
                              value=''
                              checked={
                                selectStoreIds.length === storeList.length
                                  ? true
                                  : false
                              }
                              onChange={() =>
                                handleSelectStore(null, null, true)
                              }
                            />
                            <label className='form-check-label'>All</label>
                          </div>
                        )}
                      </div>
                    </div>
                    <div>
                      <label className='form-label'>Add Sub-Category</label>
                      {subCategory.map((item, index) => {
                        return (
                          <div className='d-flex align-items-center mb-2'>
                            <input
                              className='form-control w-75'
                              placeholder='enter sub-category name ...'
                              value={item}
                              onChange={(e) =>
                                handleSetSubCategory(e.target.value, index)
                              }
                              required
                            />
                            <AiOutlinePlusCircle
                              style={{ cursor: 'pointer' }}
                              className='mx-2'
                              onClick={handlePlusSubCategory}
                            />
                            <AiOutlineMinusCircle
                              className='pointer'
                              onClick={() => handleMinusSubCategory(index)}
                            />
                          </div>
                        );
                      })}
                    </div>
                  </div>
                  <div className='modal-footer'>
                    <div className='col-md-3 px-1'>
                      <button
                        type='button'
                        className='form-control btn-outline-dark'
                        data-bs-dismiss='modal'
                        onClick={handleCancel}
                      >
                        Cancel
                      </button>
                    </div>
                    <div className='col-md-3 px-1'>
                      <button
                        type='submit'
                        className='form-control btn-outline-dark'
                      >
                        Save
                      </button>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
          <div
            className='modal fade'
            id='updateCategory'
            data-bs-backdrop='static'
            data-bs-keyboard='false'
            tabIndex='-1'
            aria-hidden='true'
          >
            <div className='modal-dialog modal-dialog-centered'>
              <div className='modal-content'>
                <div className='modal-header px-3 py-1'>
                  <h5 style={{ fontSize: '16px' }} className='modal-title'>
                    Update Category
                  </h5>
                </div>
                <form action='' onSubmit={handleUpdateCategory} id='form'>
                  <div className='modal-body'>
                    <div className='mb-3'>
                      <label className='form-label'>Category Name</label>
                      <input
                        className='form-control'
                        placeholder='enter category name ...'
                        value={category}
                        onChange={(e) => handleSetCategory(e.target.value)}
                        required
                      />
                    </div>
                    <div className='mb-3'>
                      <label className='form-label'>
                        Select Store Location
                      </label>
                      <div>
                        {storeList.map((item) => {
                          return (
                            <div className='form-check form-check-inline'>
                              <input
                                className='form-check-input'
                                type='checkbox'
                                value=''
                                checked={
                                  selectStoreIds.find((id) => {
                                    return id === item.id;
                                  }) !== undefined
                                    ? true
                                    : false
                                }
                                onChange={() =>
                                  handleSelectStore(item.id, item.name, false)
                                }
                              />
                              <label className='form-check-label'>
                                {item.name}
                              </label>
                            </div>
                          );
                        })}
                        {storeList.length !== 0 && (
                          <div className='form-check form-check-inline'>
                            <input
                              className='form-check-input'
                              type='checkbox'
                              value=''
                              checked={
                                selectStoreIds.length === storeList.length
                                  ? true
                                  : false
                              }
                              onChange={() =>
                                handleSelectStore(null, null, true)
                              }
                            />
                            <label className='form-check-label'>All</label>
                          </div>
                        )}
                      </div>
                    </div>
                    <div>
                      <label className='form-label'>Add Sub-Category</label>
                      {subCategory.map((item, index) => {
                        return (
                          <div className='d-flex align-items-center mb-2'>
                            <input
                              className='form-control w-75'
                              placeholder='enter sub-category name ...'
                              value={item}
                              onChange={(e) =>
                                handleSetSubCategory(e.target.value, index)
                              }
                              required
                            />
                            <AiOutlinePlusCircle
                              style={{ cursor: 'pointer' }}
                              className='mx-2'
                              onClick={handlePlusSubCategory}
                            />
                            <AiOutlineMinusCircle
                              className='pointer'
                              onClick={() => handleMinusSubCategory(index)}
                            />
                          </div>
                        );
                      })}
                    </div>
                  </div>
                  <div className='modal-footer'>
                    <div className='col-md-3 px-1'>
                      <button
                        type='button'
                        className='form-control btn-outline-dark'
                        data-bs-dismiss='modal'
                        onClick={handleCancel}
                      >
                        Cancel
                      </button>
                    </div>
                    <div className='col-md-3 px-1'>
                      <button
                        type='submit'
                        data-bs-dismiss='modal'
                        className='form-control btn-outline-dark'
                      >
                        Save
                      </button>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default CreateCategory;
