import logo from '../../assets/images/TigerPuffLogo.png';
import { BsCloudUpload } from 'react-icons/bs';
import { MdArrowBackIos } from 'react-icons/md';
import './admin.css';
import { useEffect, useRef, useState } from 'react';
import { PaginationFunction } from './PaginationFunction';
import { useGlobalState } from '../../context';
import { useHistory } from 'react-router';
import axios from 'axios';
export default function Setup() {
  const API_URL = process.env.REACT_APP_API_URL;
  const { currentUser } = useGlobalState();
  const history = useHistory();
  const [store, setStore] = useState([
    {
      id: 0,
      name: '',
      category: '',
      phone: '',
      logo_img: null,
      logo_img_id: null,
      cover_img: null,
      cover_img_id: null,
      address_1: '',
      address_2: '',
      city: '',
      postal_code: '',
    },
  ]);
  const { currentItems, currentPage, handleNextbtn, pagination } =
    PaginationFunction(store, 1);
  const [name, setName] = useState(currentItems[0].name);
  const [category, setCategory] = useState(currentItems[0].category);
  const [phone, setPhone] = useState(currentItems[0].phone);
  const [address1, setAddress1] = useState(currentItems[0].address_1);
  const [address2, setAddress2] = useState(currentItems[0].address_2);
  const [city, setCity] = useState(currentItems[0].city);
  const [postalcode, setPostalcode] = useState(currentItems[0].postal_code);
  const [logoImg, setLogoImg] = useState(currentItems[0].logo_img);
  const [logoImgId, setLogoImgId] = useState(currentItems[0].logo_img_id);
  const [coverImg, setCoverImg] = useState(currentItems[0].cover_img);
  const [coverImgId, setCoverImgId] = useState(currentItems[0].cover_img_id);
  const handleAddStore = (e) => {
    e.preventDefault();
    let copy = [...store];
    copy.splice(currentItems[0].id, 1, {
      id: currentItems[0].id,
      name: name,
      category: category,
      phone: phone,
      logo_img: logoImg,
      logo_img_id: logoImgId,
      cover_img: coverImg,
      cover_img_id: coverImgId,
      address_1: address1,
      address_2: address2,
      city: city,
      postal_code: postalcode,
    });
    if (currentPage === store.length) {
      copy.push({
        id: currentItems[0].id + 1,
        name: '',
        category: '',
        phone: '',
        logo_img: null,
        logo_img_id: null,
        cover_img: null,
        cover_img_id: null,
        address_1: '',
        address_2: '',
        city: '',
        postal_code: '',
      });
    }
    console.log(copy);
    setStore(copy);
  };
  const handleNext = async () => {
    let copy = [...store];
    copy.pop();
    let bodyData = [];
    copy.forEach((item) => {
      let obj = {
        name: item.name,
        category: item.category,
        portal_code: item.postal_code,
        address: item.address_1,
        other_address: item.address_2,
        city: item.city,
      };
      if (item.logo_img_id !== null) {
        obj.logo_id = item.logo_img_id;
      }
      if (item.cover_img_id !== null) {
        obj.cover_photo_id = item.cover_img_id;
      }
      bodyData.push(obj);
    });
    try {
      let res = await Promise.all(
        copy.map((item, index) =>
          axios
            .post(`${API_URL}/api/stores/`, bodyData[index], {
              headers: {
                Authorization: `Bearer ${currentUser.access_token}`,
              },
            })
            .then((response) => response.data)
        )
      );
      console.log(res);
      history.push('/admin/setup-guide');
    } catch (err) {
      console.log(err);
    }
  };
  const handleUploadImg = async (e, kind) => {
    let file = e.target.files[0];
    try {
      let res1 = await axios.post(`${API_URL}/api/file-management`, {
        file_name: file.name,
        content_type: file.type,
      });
      console.log(res1);
      if (res1.data.presign_url) {
        var config = {
          method: 'put',
          url: res1.data.presign_url,
          headers: {
            'Content-Type': file.type,
          },
          data: file,
        };
        let res2 = await axios(config);
        if (res2.statusText === 'OK') {
          let res3 = await axios.put(
            `${API_URL}/api/file-management/${res1.data.file_id}`
          );
          if (res3.data.status === true) {
            console.log(res3.data.public_url);
            if (kind === 'logo') {
              setLogoImg(file);
              setLogoImgId(res1.data.file_id);
            } else {
              setCoverImg(file);
              setCoverImgId(res1.data.file_id);
            }
          }
        }
      }
    } catch (err) {
      console.log(err);
    }
  };
  useEffect(() => {
    setName(currentItems[0].name);
    setCategory(currentItems[0].category);
    setPhone(currentItems[0].phone);
    setAddress1(currentItems[0].address_1);
    setAddress2(currentItems[0].address_2);
    setCity(currentItems[0].city);
    setPostalcode(currentItems[0].postal_code);
    setLogoImg(currentItems[0].logo_img);
    setLogoImgId(currentItems[0].logo_img_id);
    setCoverImg(currentItems[0].cover_img);
    setCoverImgId(currentItems[0].cover_img_id);
  }, [currentPage]);
  return (
    <div className='d-flex flex-column align-items-center'>
      <img src={logo} alt='' className='admin__setup__logo' />
      <div className='container-fluid'>
        <div className='row'>
          <form
            className='col-lg-6 offset-lg-3 col-md-12 shadow rounded px-3 py-3'
            onSubmit={handleAddStore}
          >
            <h2>Setup Store</h2>
            <div className='row mb-3'>
              <div className='col-md-6 d-flex flex-column justify-content-between'>
                <div className='col-12 mb-3'>
                  <label className='form-label'>Store Name</label>
                  <input
                    className='form-control'
                    placeholder='Enter restaurant Name'
                    value={name}
                    onChange={(e) => {
                      setName(e.target.value);
                    }}
                    required
                  />
                </div>
                <div className='col-12 mb-3'>
                  <label className='form-label'>Store Category</label>
                  <input
                    className='form-control'
                    placeholder='Enter store category'
                    value={category}
                    onChange={(e) => setCategory(e.target.value)}
                    required
                  />
                </div>
                <div className='col-12'>
                  <label className='form-label'>Phone Number</label>
                  <div className='input-group'>
                    <div className='input-group-text'>+63</div>
                    <input
                      type='tel'
                      className='form-control'
                      value={phone}
                      onChange={(e) => setPhone(e.target.value)}
                      required
                    />
                  </div>
                </div>
              </div>
              <div className='col-md-6'>
                <div className='col-12 d-flex flex-column mb-3'>
                  <label className='form-label'>Upload Logo</label>
                  <label className='upload-image'>
                    {!logoImg && (
                      <span>
                        <BsCloudUpload />
                      </span>
                    )}
                    {logoImg && (
                      <img
                        className='w-75 h-100'
                        style={{ objectFit: 'cover' }}
                        src={URL.createObjectURL(logoImg)}
                        alt=''
                      />
                    )}
                    <input
                      style={{ display: 'none' }}
                      type='file'
                      id='file'
                      accept='.png,.jpeg,.jpg'
                      onChange={(e) => {
                        handleUploadImg(e, 'logo');
                      }}
                    />
                  </label>
                </div>
                <div className='col-12 d-flex flex-column'>
                  <label className='form-label'>Upload Cover Photo</label>
                  <label className='upload-image'>
                    {!coverImg && (
                      <span>
                        <BsCloudUpload />
                      </span>
                    )}
                    {coverImg && (
                      <img
                        className='w-75 h-100'
                        style={{ objectFit: 'cover' }}
                        src={URL.createObjectURL(coverImg)}
                        alt=''
                      />
                    )}
                    <input
                      style={{ display: 'none' }}
                      type='file'
                      id='file'
                      accept='.png,.jpeg,.jpg'
                      onChange={(e) => {
                        handleUploadImg(e, 'cover');
                      }}
                    />
                  </label>
                </div>
              </div>
            </div>
            <div className='row'>
              <label className='form-label'>Address</label>
              <div className='col-md-6 mb-3'>
                <input
                  className='form-control'
                  placeholder='Address 1'
                  value={address1}
                  onChange={(e) => setAddress1(e.target.value)}
                  required
                />
              </div>
              <div className='col-md-6 mb-3'>
                <input
                  className='form-control'
                  placeholder='Address 2'
                  value={address2}
                  onChange={(e) => setAddress2(e.target.value)}
                />
              </div>
            </div>
            <div className='row mb-3'>
              <div className='col-lg-6 col-md-12'>
                <div className='row'>
                  <div className='col-6'>
                    <label className='form-label'>City</label>
                    <input
                      className='form-control'
                      placeholder='City'
                      value={city}
                      onChange={(e) => setCity(e.target.value)}
                      required
                    />
                  </div>
                  <div className='col-6'>
                    <label className='form-label'>Postal Code</label>
                    <input
                      className='form-control'
                      placeholder='Postal Code'
                      value={postalcode}
                      onChange={(e) => setPostalcode(e.target.value)}
                      required
                    />
                  </div>
                </div>
              </div>
            </div>
            <div className='row'>
              <div className='col-md-6'>{pagination}</div>
              <div className='col-md-6'>
                <div className='row'>
                  <div className='col-6'>
                    {store.length === currentPage ? (
                      <button
                        style={{
                          color: '#C4C4C4',
                          backgroundColor: '#ffffff',
                          border: '1px solid #C4C4C4',
                        }}
                        type='submit'
                        className='form-control btn-outline-dark'
                      >
                        Add Store
                      </button>
                    ) : (
                      <button
                        style={{
                          color: '#C4C4C4',
                          backgroundColor: '#ffffff',
                          border: '1px solid #C4C4C4',
                        }}
                        type='submit'
                        className='form-control btn-outline-dark'
                      >
                        Update Store
                      </button>
                    )}
                  </div>
                  <div className='col-6'>
                    <button
                      style={{ color: 'white', backgroundColor: '#FE7B50' }}
                      type='button'
                      className='form-control'
                      onClick={handleNext}
                    >
                      Next
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
}
